<template>
    <div id="ComponenteProcuctos">
        <h2 class="mb-4">Productos disponibles</h2>
        <div class="container d-flex flex-wrap justify-content-around">
            <div v-for="(datos, index) in informacionComida" :key="index" class="d-flex align-items-stretch">
                <div class="card mb-5" style="width: 13rem;">
                    <img :src="datos.imagen" class="card-img-top" alt="datos.titulo">
                    <div class="card-body">
                        <h5 class="card-title">{{ datos.titulo }}</h5>
                        <p class="card-text">{{ datos.descripcion }}</p>
                        <p class="card-text"><b>${{ datos.costo }}</b></p>
                        <a href="#" class="btn btn-info" v-on:click.prevent="agregarComida(index)">Agregar</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'ComponenteProcuctos',
    props: {
        informacionComida: {
            type: Array,
            required: true
        }
    },
    data: function() {
        return {

        }
    },
    methods: {
        agregarComida: function(index) {
            this.$emit('comidaSeleccionada', index);
        }
    }
}
</script>

