<template>
    <div id="ComponentePago">
        <h3 class="mb-4">Pago</h3>
        <table class="table table-hover text-start">
            <thead class="table-secondary">
                <tr>
                    <th>Producto</th>
                    <th>Precios</th>
                    <th>Eliminar</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(info, index) in facturacionComida" :key="index">
                    <td>{{ info.nombreComidaEnviada }}</td>
                    <td><b>{{ info.precioComidaEnviada }}</b></td>
                    <td class="text-center"><button @click="eliminarProducto(index)" class="btn btn-dark btn-sm">X</button></td>
                </tr>
            </tbody>
        </table>
        <button v-on:click.prevent="calcularTotal" class="btn btn-warning mb-4">Calcular total</button>
        <div v-if="mostrar==true">
            <p>El sub total de su compra es: <b>${{ acumulado }}</b></p>
            <p>El iva es: <b>${{ iva }}</b></p>
            <p><b>El total a pagar es: ${{ valorFinalAPagar }}</b></p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ComponentePago',
    props: {
        facturacionComida: {
            type: Array,
            required: true
        }
    },
    data: function() {
        return {
            acumulado: 0,
            iva: 0,
            valorFinalAPagar: 0,
            mostrar: false
        }
    },
    methods: {
        calcularTotal: function() {
            this.acumulado = 0;
            for(let datos of this.facturacionComida) {
                this.acumulado = this.acumulado + datos.precioComidaEnviada;
            }
            this.calcularIva();
            this.mostrar = true;
        },
        calcularIva: function() {
            this.iva = this.acumulado*19/100;
            this.valorFinalAPagar = this.acumulado + this.iva;
        },
        eliminarProducto: function(index) {
            this.mostrar = false;
            this.acumulado = 0;
            this.$emit('eliminar', index)
        }
    }
}
</script>

