<template>
  <div class="eventModifierSelf">
    <h2>Event modifier SELF</h2>
    <div id="abuelo" v-on:click="saludo('Hola soy el abuelo')"> div abuelo
      <!--Al colocar el modificador self en el evento on click, no se va propagar el evento del padre, el unico que pueden invocar el evento en el padre es el mismo-->
      <div id="padre" v-on:click.self="saludo('Hola soy el padre')"> div padre
        <div id="hijo" v-on:click="saludo('Hola soy el hijo')">
          div hijo
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'eventModifierSelf',
  props: {
    msg: String
  },
  methods: {
    saludo: function(str) {
      alert(str)
    }
  }
}
</script>

