<template>
  <div class="hello">
    <h2>Event modifier STOP</h2>
    <!--problema de propagacion surge con uso de mismo metodo en elementos anidados (elemento ejecuta metodo propio y el de todos sus ascendientes)-->
    <div id="abuelo" v-on:click="saludo('Hola soy el abuelo')"> div abuelo
      <div id="padre" v-on:click.stop="saludo('Hola soy el padre')"> div padre
        <!--Stop detiene la propagacion del evento onclick en los ancestros del div hijo (padre y cualquiera que tenga ancestro)-->
        <div id="hijo" v-on:click.stop="saludo('Hola soy el hijo')">
          div hijo
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  methods: {
    saludo: function(str) {
      alert(str)
    }
  }
}
</script>

